import React from 'react';
import HomePage from './pages/HomePage'
import ContactPage from './pages/ContantPage'
import ContactDetails from './pages/ContantDetailsPage'
import StatisticsPage from './pages/StatisticsPage'
import ContactEdit from './pages/ContactEditPage'
import { BrowserRouter as Router, Route, NavLink, Switch } from 'react-router-dom'

import './App.css';
const Links = () => (
  <nav className="nav-bar-container">
    <NavLink exact activeClassName ="active" to="/">Home</NavLink> |
    <NavLink activeClassName="active" to="/contacts">Contacts</NavLink> | 
    <NavLink activeClassName="active" to="/statistics">statistics</NavLink>
  </nav>
)
function App() {
  return (
    <Router>
      <Links/>
    <div className="App">
      <Switch>
      <Route exact path="/" component={HomePage} />
      <Route exact path="/contacts" component={ContactPage} />
      <Route exact path="/contacts/edit" component={ContactEdit} />
      <Route exact path="/contacts/:id?" component={ContactDetails} />

      <Route exact path="/contacts/edit/:id?" component={ContactEdit} />
      <Route exact path="/statistics" component={StatisticsPage} />
      </Switch>
    </div>
    </Router>
  );
}

export default App;
