
import PropTypes from 'prop-types';
import React from 'react';
import { Link } from "react-router-dom";




const ContactPreview = (props) => {
    return (
        <section className="preview-container">
            <Link to={`/contacts/${props.contact._id}`}>
                <div className="img-container"><img alt='' src={`https://robohash.org/${props.contact.name}?set=set3&size=100x100 `} /></div>
                <h2>{props.contact.name}</h2>
                <h2>Phone: {props.contact.phone}</h2>
                <h2>Email: {props.contact.email}</h2>
            </Link>
        </section>
    )
}

export default ContactPreview

ContactPreview.propTypes = {
    contact: PropTypes.object,
}




