
import axios from 'axios'

async function getRate() {
   const res = await axios.get('https://blockchain.info/tobtc?currency=USD&value=1')
   const rate = res.data
   return rate
}
getMarketPrice()
async function getMarketPrice() {
   const res = await axios.get('https://api.blockchain.info/charts/market-price?timespan=5months&format=json&cors=true')
   const marketPrice = res.data.values
   return marketPrice
}
async function getConfirmedTransactions() {
   const res = await axios.get(`https://api.blockchain.info/charts/n-transactions?timespan=50days&format=json&cors=true`)
   return res.data.values
}

export default {
   getRate,
   getMarketPrice,
   getConfirmedTransactions
}