import React, { Component } from 'react';
import Charts from '../cmps/Chart'
import bitcoinService from '../services/BitcoinService'
class StatisticsPage extends Component {

    state = {
        marketPrice: null,
        transactions: null
    }
    async componentDidMount() {
        let marketPrice = await bitcoinService.getMarketPrice()
        let transactions = await bitcoinService.getConfirmedTransactions()
        this.setState({ marketPrice, transactions })
        console.log(this.state.marketPrice);

    }

    render() {
        return (
            <section>

                <h1>StatisticsPage!</h1>
                {this.state.transactions && this.state.marketPrice &&
                 <Charts
                  transactions={this.state.transactions}
                    marketPrice={this.state.marketPrice} 
                    >
                    </Charts>}
            </section>
        )
    }
}
export default StatisticsPage