
import React from 'react';



const ContactFilter = (props) => {

let handleChange=(event)=>{
let txt = event.target.value
props.filter(txt)
}

    return (
        <section>
                <h1>filter</h1>
                <input onChange={props.filter} type="text" />
                {/* <input onChange={handleChange} type="text" /> */}
        </section>
    )
}
export default ContactFilter






