import React, { Component } from 'react';
import ContactService from '../services/ContactService'
import ContactPreview from '../cmps/ContactPreview'
import {NavLink } from 'react-router-dom'



class ContactDetails extends Component {
    state = {
        user: {}
    }
    async componentDidMount() {
        let user = await ContactService.getContactById(this.props.match.params.id)
        this.setState({ user })
    }

    render() {
        return (
            <section>
                <h1>Contact Details!</h1>

                <ContactPreview contact={this.state.user}></ContactPreview>
    <NavLink  to={`/contacts/edit/${this.state.user._id}`}>Edit user</NavLink> |
                
            </section>
        )
    }
}
export default ContactDetails