import React, { Component } from 'react';
import ContactService from '../services/ContactService'
import ContactList from '../cmps/ContactList'
import ContactFilter from '../cmps/ContactFilter'
import { BrowserRouter as Router, NavLink } from 'react-router-dom'

class ContactPage extends Component {
    state = {
        users: [],
        usersToShow: []
    }

    async componentDidMount() {
        let users = await ContactService.getContacts()
        this.setState({ users })
        this.filter()
    }
    filter = (event) => {
        let txt = ''
        if (!event) txt = ''
        else txt = event.target.value
        console.log(event);

        this.setState({
            usersToShow: this.state.users.filter((contact) => {
                return contact.name.includes(txt) ||
                    contact.phone.includes(txt) ||
                    contact.email.includes(txt)
            })
        })
    }

    render() {
        return (
            <section >
                <h1>contact page</h1>
                <NavLink to={`/contacts/edit`}>add user</NavLink>
                <ContactFilter
                    filter={this.filter}></ContactFilter>
                <ContactList contacts={this.state.usersToShow}></ContactList>

            </section>

        )
    }
}

export default ContactPage