import React, { Component } from 'react';
import { Redirect, NavLink} from 'react-router-dom'
import contactService from '../services/ContactService'
// import { log } from 'util';
// import { async } from 'q';
class ContactEdit extends Component {
    state = {
        user: {
            name: '',
            email: '',
            phone: ''
        },
        redirectToDetailes: false,
        redirectToContacts:false
    }

    async  componentDidMount() {
        let userId = this.props.match.params.id
        if (userId) {
            let user = await contactService.getContactById(userId)
            this.setState({ user })
        }
    }
    handleChange = (field) => {
        return ev => {
            const user = { ...this.state.user, [field]: ev.target.value }
            this.setState({ user });
        }
    }
    saveContact = async (ev) => {
        ev.preventDefault()
      await  contactService.saveContact(this.state.user)
                this.setState({ redirectToDetailes: true });
    }
    deleteContact = async(ev) => {
       await contactService.deleteContact(this.state.user._id)
            this.setState({redirectToContacts:true})
    }
    render() {
        if (this.state.redirectToDetailes) {
            return <Redirect to={`/contacts/${this.state.user._id}`} />
        }
        if (this.state.redirectToContacts) {
            return <Redirect to={`/contacts`} />
        }
        var button;
       if (this.state.user._id) button = <button onClick={this.deleteContact}>üóëDeleteüóë</button>
        return (
            <section>
            <NavLink to="/contacts">‚¨ÖÔ∏è</NavLink>
            <form className="edit-form" onSubmit={this.saveContact} >
                Name:  <input className="edit-input"
                    type="text" defaultValue={this.state.user.name || ''} onChange={this.handleChange('name')} />
                Email: <input className="edit-input"
                    type="email" defaultValue={this.state.user.email || ''} onChange={this.handleChange('email')} />
                Phone: <input className="edit-input"
                    type="tel" defaultValue={this.state.user.phone || ''} onChange={this.handleChange('phone')} />
                <button type="submit">Save</button>
                {button}
            </form>
        </section>
        )
    }
}
export default ContactEdit